import { Component, OnInit,AfterViewInit } from '@angular/core';
import {SearchService} from '../../services/search.service'

@Component({
  selector: 'app-reader',
  templateUrl: './reader.component.html',
  styleUrls: ['./reader.component.css']
})
export class ReaderComponent implements OnInit ,AfterViewInit  {
  container : HTMLElement; 

  sentences : string[]
  allSentences : string[]
  indexNum :number;
  totalLIne :number;
  constructor(private searchService: SearchService) {
    this.sentences=new Array()
   }

   ngAfterViewInit() {         
    this.container = document.getElementById("readerContainer");           
    this.container.scrollTop = this.container.scrollHeight;     
  }  


  ngOnInit() {
    this.searchService.reader("").subscribe(data=>{
      this.allSentences=data;
      this.totalLIne=this.allSentences.length;
      if(this.totalLIne>0){
       this.sentences.push(this.allSentences[0])
        this.indexNum=0;
      }
    });
  }

  nextSentence(){
    this.indexNum++;
    if(this.indexNum<this.totalLIne){
      this.sentences.push(this.allSentences[this.indexNum]);
    }
    this.ngAfterViewInit();
  }

}
