import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SearchService {

  constructor(private httpClient: HttpClient) { }

  public searchSolr(queryTerm : string) : Observable<any>{
    return this.httpClient.get("http://localhost:8983/solr/abc/select?df=word&indent=off&rows=100&sort=word_s+asc&wt=json&q="+queryTerm);
  }
  public reader(queryTerm : string) : Observable<any>{
    return this.httpClient.get("/edification/api/read");
  }

  public dirFiles(dirPath : string) : Observable<any>{
    var data={"directoryPath":"C:\\work\\opensource\\engdata\\scripts"}
    return this.httpClient.post("/edification/api/dirFiles",data);
  }

}
